@import "_variables";

.header {
  @include setup(
    $backgroundColor: map-get($colors, "deepTeal"),
    $backgroundImage: "/images/tile_drkteal.gif",
  );
}

.main:not(.videos) {
  @include setup(
    $backgroundColor: map-get($colors, "deepTeal"),
    $backgroundImage: "/images/tile_drkteal.gif",
    $linkActiveColor: map-get($colors, "electricViolet1"),
    $linkColor: map-get($colors, "white"),
    $linkVisitedColor: map-get($colors, "anakiwa1"),
  );
}

.main.videos {
  @include setup(
    $backgroundColor: map-get($colors, "ivory"),
  );
}
